import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { MenuSliceActions } from "../../store/menu-slice/MenuSlice";
import useHttp from "../../hooks/use-Http";

import classes from "./SpecialMenu.module.css";
import SubHeading from "../Header/SubHeading";
import SpecialMenuItem from "./SpecialMenuItem";

const SpecialMenu = () => {
  const juices = useSelector((state) => state.menu.juices);
  const dishes = useSelector((state) => state.menu.dishes);
  const isAuth = useSelector((state) => state.auth.isAuth);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { sendRequest, error } = useHttp();

  const toAuthHandler = () => {
    navigate(`/auth`, { replace: true });
  };

  useEffect(() => {
    const dataGrabber = (data) => {
      const loadedDishes = Object.keys(data).map((key) => ({
        id: key,
        desc: data[key].desc,
        title: data[key].title,
        tags: data[key].tags,
        price: data[key].price,
      }));

      dispatch(MenuSliceActions.addDishes(loadedDishes));
    };

    sendRequest(
      {
        url: `https://react-tester-9d1b5-default-rtdb.europe-west1.firebasedatabase.app/dishes.json`,
      },
      dataGrabber
    );
  }, [sendRequest, dispatch]);

  useEffect(() => {
    const dataGrabber = (data) => {
      const loadedJuices = Object.keys(data).map((key) => ({
        id: key,
        desc: data[key].desc,
        title: data[key].title,
        tags: data[key].tags,
        price: data[key].price,
      }));

      dispatch(MenuSliceActions.addJuices(loadedJuices));
    };

    sendRequest(
      {
        url: `https://react-tester-9d1b5-default-rtdb.europe-west1.firebasedatabase.app/juices.json`,
      },
      dataGrabber
    );
  }, [sendRequest, dispatch]);

  return (
    <section
      className={`flex-center section-padding ${classes.specialmenu}`}
      id="menu"
    >
      <div className={classes[`specialmenu-title`]}>
        <SubHeading title="Menu that fits your palette" />
        <h1 className="headtext-cormorant">Gilani's Special</h1>
      </div>

      <div className={classes[`specialmenu-menu`]}>
        <div
          style={{ marginRight: `10rem` }}
          className={`flex-center ${classes[`specialmenu-menu-juices`]}`}
        >
          <p className={classes[`specialmenu-menu-heading`]}>Juices</p>
          {error && (
            <p className="p-opensans">
              {error.message} Unable To Load Juice Menu... please try again!
            </p>
          )}
          {!error && (
            <ul className={classes[`specialmenu-menu-lists`]}>
              {juices.map(({ id, title, tags, price, desc }) => (
                <SpecialMenuItem
                  key={id}
                  id={id}
                  title={title}
                  desc={desc}
                  tags={tags}
                  price={price}
                />
              ))}
            </ul>
          )}
        </div>
        {/* <figure className={classes[`specialmenu-menu-img`]}>
          <img src={images.menu} alt="Cold menu beverages" />
        </figure> */}
        <div className={`flex-center ${classes[`specialmenu-menu-dishes`]}`}>
          <p className={classes[`specialmenu-menu-heading`]}>Dishes</p>
          {error && (
            <p className="p-opensans">
              {error.message} Unable To Load Dishes Menu... please try again!
            </p>
          )}
          {!error && (
            <ul className={classes[`specialmenu-menu-lists`]}>
              {dishes.map(({ id, title, tags, price, desc }) => (
                <SpecialMenuItem
                  key={id}
                  id={id}
                  title={title}
                  desc={desc}
                  tags={tags}
                  price={price}
                />
              ))}
            </ul>
          )}
        </div>
      </div>
      {!isAuth && (
        <div style={{ marginTop: `15px` }}>
          <button
            type="button"
            className="custom-button"
            onClick={toAuthHandler}
          >
            View More
          </button>
        </div>
      )}
    </section>
  );
};

export default SpecialMenu;
