import React from "react";
import classes from "./SpecialMenuItem.module.css";
import { useSelector, useDispatch } from "react-redux";
import { CartSliceActions } from "../../store/cart-slice/CartSlice";
import { Link } from "react-router-dom";

const SpecialMenuItem = (props) => {
  const isAuth = useSelector((state) => state.auth.isAuth);
  const dispatch = useDispatch();
  const price = new Intl.NumberFormat(navigator.language, {
    style: `currency`,
    currency: `GBP`,
  }).format(props.price.toFixed(2));

  const addItemHandler = () => {
    dispatch(
      CartSliceActions.addItem({
        id: props.id,
        quantity: 1,
        title: props.title,
        price: props.price,
        total: props.price,
      })
    );
  };

  const path = `/home/${props.id}`;
  return (
    <li className={`${classes.specialmenuitem}`} key={props.id}>
      <div className={classes[`specialmenuitem-head`]}>
        <div className={classes[`specialmenuitem-name`]}>
          <h2 className="p-cormorant">{props.title}</h2>
        </div>
        <div style={{ gap: `2rem` }} className="flex-center">
          <div className={classes.seperator} />
          <div className={classes[`specialmenuitem-price`]}>
            <p className="p-cormorant">{price}</p>
          </div>
        </div>
      </div>
      {isAuth && (
        <div className={classes[`specialmenuitem-desc`]}>
          <p className="p-opensans" style={{ color: `#ccc` }}>
            {props.desc}
          </p>
        </div>
      )}
      <div className={classes[`specialmenuitem-tags`]}>
        <p className="p-opensans" style={{ color: `#AAA` }}>
          {props.tags}
        </p>
        {isAuth && (
          <div className={classes[`specialmenuitem-actions`]}>
            <Link to={path}>View More</Link>
            <button
              type="button"
              className="custom-button"
              onClick={addItemHandler}
            >
              Add
            </button>
          </div>
        )}
      </div>
    </li>
  );
};

export default SpecialMenuItem;
